* {
    margin: 0;
    padding: 0;
    box-sizing: border-box !important;
}

p,
h1,
h2,
h3,
h4,
h5 {
    margin-bottom: 0;
}

body {
    height: 2000px;
    background-attachment: fixed;
}

.parallax-window {
    // min-height: 1000px;
    background: transparent;
}

.menu,
.hamburger {
    // background: rgb(0, 140, 255) !important;
    color: rgb(0, 0, 0) !important;
}

.hamburger__label::before {
    background: rgb(0, 0, 0) !important
}

.navbar-light,
.bg-light {
    background: rgba(255, 255, 255, 0) !important;
}

.menu__link:hover {
    color: rgb(255, 230, 0);
    text-decoration: none;
}

.header {
    display: none;
}

header {


    .header_info {
        padding: 15px 0 15px 0px;
        background: rgba(206, 206, 206, 0.171);


        .container {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;

            .hid {
                display: flex;
                justify-content: center;
                align-items: flex-start;
                padding: 0 20px;

                .hid_icon {
                    img {
                        width: 40px;
                        height: 40px;
                    }

                    margin-right: 20px;
                }


                .hid_text {
                    display: flex;
                    justify-content: center;
                    align-items: flex-start;
                    flex-direction: column;

                    h3 {
                        font-family: 'Roboto', sans-serif;
                        color: #5a9600;
                        letter-spacing: 0.3px;
                        text-transform: uppercase;
                        font-size: 11px;
                        font-weight: bold;
                        margin-bottom: 5px;
                    }

                    p {
                        font-family: 'Roboto', sans-serif;
                        color: #000000;
                        letter-spacing: 0.3px;

                        font-size: 15px;
                        font-weight: 400;
                    }
                }

                a {
                    height: 65px;
                    width: 200px;
                    background: linear-gradient(to bottom right, #71bd00 0%, #199400 100%);
                    text-decoration: none;
                    display: flex;
                    justify-content: center;
                    border-radius: 5px;
                    align-items: center;

                    .hid_icon {
                        img {
                            width: 30px;
                            height: 30px;
                        }

                        margin-right: 20px;
                    }

                    h3 {
                        font-size: 14px;
                        color: white;
                        font-family: 'Roboto', sans-serif;
                        text-transform: uppercase;

                    }
                }

                a:hover {
                    background: linear-gradient(to bottom right, #199400 0%, #71bd00 100%);

                }
            }

            .hid:last-child {
                display: flex;
                justify-content: flex-end;
                align-items: center;
            }

        }
    }

    .header_nav {
        padding: 10px;
        width: 100%;

        .container {

            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-wrap: wrap;
        }



        .logo {
            display: flex;
            justify-content: center;
            align-items: center;

            img {
                width: 200px;
                height: 80px;
            }
        }


        h3 {
            font-family: 'Roboto', sans-serif;
            font-weight: 600;
            font-size: 25px;
            color: #392d88;
            text-align: center;
        }

        .nav_media {
            display: flex;
            justify-content: center;
            align-items: center;

            .nav_icon {
                margin-left: 10px;
                margin-right: 10px;

                img {
                    width: 40px !important;
                    height: 40px !important;
                }
            }
        }



        .social-media {
            margin-right: 80px;

            .social {

                img {
                    width: 40px;
                    height: 40px;
                    margin-left: 10px;
                    margin-right: 10px;
                }
            }
        }

    }

}

drawer-list {
