// =============main-slider=======================


.plus {
    width: 100%;
    height: 200px;

    background: rgba(38, 133, 0, 0.356);

}

.slider {
    .container-fluid {
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.swiper-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    .swiper-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .swiper-slide {
            overflow: visible;
            max-height: 560px;
            min-height: 560px;
            position: relative;
            z-index: 3;
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 25px 80px;


            // background: #09680091;
            // border: 1px solid #CACACA;
            box-sizing: border-box;
            // box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);

            .slide-img {
                img {
                    max-width: 508px;
                    max-height: 508px;
                }
            }

            .slide-text {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                // font-family: 'Lobster', cursive;
                font-family: 'Montserrat', sans-serif;
                width: 60%;

                h4 {
                    font-style: normal;
                    line-height: 10%;
                    font-weight: 600;
                    font-size: 45px;
                    line-height: 43px;
                    color: #ffffff;
                    text-align: center;
                }

                p {
                    margin-top: 20px;
                    font-style: normal;
                    font-weight: 400;
                    font-size: 18px;
                    line-height: 30px;
                    color: #ffffff;
                }

                a {
                    // box-shadow: 0px 5px 7px rgba(0, 0, 0, 0.25);
                    margin-top: 20px;
                    width: 246px;
                    height: 59px;
                    background: #e0e0e0;
                    // box-shadow: 20px 20px 34px #bebebe,
                    //     -20px -20px 34px #ffffff;
                    font-style: normal;
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 30px;
                    color: #000000;
                    text-decoration: none;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: 0.3s;
                }

                a:hover {
                    box-shadow: 0px 0px 0px 2px #ffcc01;
                }
            }

        }

    }
}

.slider {
    padding: 0px 0px;
    position: relative;
}

.ss1 {
    background: url(../img/mountains-nature-landscape-min.jpg)50% 100% /cover;
}

.ss2 {
    background: url(../img/landscape-336542_1920.jpg)50% 100% /cover;
}

.swiper-button-prev {
    top: 275px;
    left: 100px;
    position: absolute;
    width: 54px;
    height: 54px;
    background: #ffffff00;
    border: 2px solid #ffffff;
    border-radius: 50%;
    box-sizing: border-box;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
    padding-right: 3px;

    img {
        width: 15px;
        height: 25px;

    }
}

.swiper-button-next {
    top: 275px;
    right: 100px;
    position: absolute;
    width: 54px;
    height: 54px;
    background: #ffffff00;
    border: 2px solid #ffffff;
    border-radius: 50%;
    box-sizing: border-box;
    box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.25);
    padding-left: 3px;

    img {
        width: 15px;
        height: 25px;

    }
}

.swiper-button-prev:focus,
.swiper-button-next:focus {
    outline: none;
}

.swiper-button-prev::after,
.swiper-button-next::after {
    display: none;
}

