.about {
    // background: url(../img/leaves.jpg) 50% 50% / cover;
    padding: 50px 0;
    font-family: 'Rubik', sans-serif;

    .container {
        display: flex;

    }

    .about_left {
        display: flex;
        justify-content: center;
        align-items: flex-start;
        flex-direction: column;

        h3 {
            font-weight: 600;
            margin-bottom: 30px;
            font-size: 40px;
        }

        p {
            font-size: 17px;
            margin-bottom: 30px;
        }

        a {
            background: rgba(0, 141, 12, 0.774);

            padding: 16px 40px;
            font-size: 16px;
            letter-spacing: 1px;
            text-decoration: none;
            color: white;
            text-transform: uppercase;
            border-radius: 10px;
            transition: 0.3s;
        }

        a:hover {
            // background: rgba(0, 0, 0, 0.774);
            background: rgba(0, 119, 255, 0);
            border: 1px rgb(0, 0, 0) solid;
            color: black;
            margin-left: 20px;
        }
    }

    .about_right {
        display: flex;
        justify-content: center;
        align-content: center;

        form {
            // max-height: 300px;
            width: 400px;
            border-radius: 10px;
            padding: 40px 30px;
            display: flex;
            justify-content: space-between;
            align-content: center;
            flex-direction: column;
            background: linear-gradient(to top right, rgba(0, 71, 6, 0.774), rgba(0, 202, 17, 0.774));


            h3 {
                text-align: center;
                font-weight: 600;
                margin-bottom: 20px;
                font-size: 30px;
                color: white;
            }

            input {
                border-radius: 5px;
                padding: 10px 20px;
                height: 45px;
                width: 100%;
                margin-bottom: 30px;
                outline: none;
                border: none;
            }

            input::placeholder {
                color: rgb(105, 105, 105);
                font-weight: 600;

            }

            button {
                border-radius: 5px;
                height: 45px;
                border: none;
                outline: none;
                font-weight: 600;
                font-size: 20px;
                background: rgba(0, 0, 0, 0);
                border: 1px white solid;
                color: white;
                transition: 0.3s;
            }

            button:hover {
                background: rgb(255, 255, 255);
                color: rgba(0, 71, 6, 0.774);
            }
        }
    }
}


